"use client";
import { useState, useEffect } from "react";

export default function CookieBanner() {
  const [consent, setConsent] = useState(true);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
    const hasConsent = localStorage.getItem("cookie-consent") === "accepted";
    setConsent(hasConsent);
  }, []);

  const handleAccept = () => {
    localStorage.setItem("cookie-consent", "accepted");
    setConsent(true);
    
    // Reload to enable analytics
    if (typeof window !== "undefined" && (window as any).gtag) {
      (window as any).gtag("consent", "update", {
        analytics_storage: "granted"
      });
    }
  };

  const handleDecline = () => {
    localStorage.setItem("cookie-consent", "declined");
    setConsent(true);
  };

  if (!mounted || consent) {
    return null;
  }

  return (
    <div className="fixed bottom-0 inset-x-0 bg-black text-white p-4 shadow-lg z-50">
      <div className="max-w-4xl mx-auto flex flex-col sm:flex-row items-center justify-between gap-4">
        <p className="text-sm">
          We use cookies to improve your experience and analyze site traffic. 
          By clicking "Accept", you consent to our use of cookies.{" "}
          <a href="/legal/privacy" className="underline hover:text-gray-300">
            Learn more
          </a>
        </p>
        <div className="flex gap-3 flex-shrink-0">
          <button
            onClick={handleDecline}
            className="px-4 py-2 text-sm border border-white rounded hover:bg-white hover:text-black transition-colors"
          >
            Decline
          </button>
          <button
            onClick={handleAccept}
            className="px-4 py-2 text-sm bg-white text-black rounded hover:bg-gray-200 transition-colors"
          >
            Accept
          </button>
        </div>
      </div>
    </div>
  );
}
