"use client";
import { useEffect } from "react";

export default function GAEvents() {
  useEffect(() => {
    const trackEvent = (eventName: string, params?: Record<string, any>) => {
      if (typeof window !== 'undefined' && (window as any).gtag) {
        (window as any).gtag("event", eventName, {
          send_to: process.env.NEXT_PUBLIC_GA_MEASUREMENT_ID,
          ...params
        });
      }
    };

    // Track page view
    trackEvent("page_view");

    // Track pricing clicks
    document.querySelectorAll('a[href*="buy.stripe.com"]').forEach(link => {
      link.addEventListener("click", () => {
        trackEvent("click_subscribe", {
          link_url: (link as HTMLAnchorElement).href
        });
      });
    });

    // Track support form submissions
    document.querySelectorAll('form[action*="/api/support"]').forEach(form => {
      form.addEventListener("submit", () => {
        trackEvent("support_submit");
      });
    });
  }, []);

  return null;
}
