"use client";
import { useEffect } from "react";

export default function Analytics() {
  useEffect(() => {
    // GoatCounter lightweight analytics (replace data-goatcounter later if you get an account)
    const s = document.createElement("script");
    s.src = "https://gc.zgo.at/count.js";
    s.async = true;
    s.setAttribute("data-goatcounter", (process.env.NEXT_PUBLIC_GOATCOUNTER_URL || "https://example.goatcounter.com") + "/count");
    document.body.appendChild(s);
    return () => { document.body.removeChild(s); };
  }, []);
  return null;
}
