"use client";
import { useEffect, useState } from "react";

export default function UTMBanner() {
  const [show, setShow] = useState(false);
  
  useEffect(() => {
    const p = new URLSearchParams(window.location.search);
    const utm = ["utm_source", "utm_medium", "utm_campaign", "utm_content", "utm_term"];
    const has = utm.some(k => p.has(k));
    
    if (has) {
      localStorage.setItem("echo_attrib", JSON.stringify(Object.fromEntries(p)));
      setShow(true);
    }
    
    // soft trim after store (no reload)
    if (has) {
      const u = new URL(window.location.href);
      utm.forEach(k => u.searchParams.delete(k));
      window.history.replaceState({}, "", u.toString());
    }
  }, []);
  
  if (!show) return null;
  
  return (
    <div className="fixed bottom-4 right-4 z-50 rounded-md border bg-white/90 backdrop-blur px-3 py-2 text-xs shadow">
      <span className="mr-3">Attribution captured.</span>
      <button onClick={() => setShow(false)} className="underline">Dismiss</button>
    </div>
  );
}
